@import url(https://fonts.googleapis.com/css?family=Quicksand);
/* body,html {
    height: 100%;
} */

.landingBackground {
    background-image: linear-gradient(#BDFFF3, #4AC29A);
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 100%;
    max-width: 100% !important;
}

#title {
    font-size: 500%;
    color: whitesmoke;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 26%;
}

#logoIcon {
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-size: 1000%;
    color: whitesmoke;
    margin-top: 10%;
}

#signIn, #signUp {
    display: block;
    margin: 5%;
    width: 50%;
    color: black;
    border: none;
    background-color: #fff;
    box-shadow: 0 8px 20px rgb(56, 158, 124); 
}

#signIn {
    opacity: .6;
    margin: auto;
    margin-top: 40%;
    margin-bottom: 5%;
}

#signUp {
    opacity: .8;
    margin: 0 auto;
}

@media screen and (max-width: 1027px) {
    #title {
        margin-top: 27%;
    }
    #signIn {
        margin-top: 42%;
        margin-bottom: 4%;
    }
}

@media screen and (max-width: 890px) {
    #title {
        margin-top: 32%;
    }
    #signIn, #signUp {
        width: 65%;
    }
    #signIn {
        margin-top: 50%;
    }
}

@media screen and (max-width: 768px) {
    #logoIcon {
        margin-top: 12%;
    }
    #title {
        margin-top: 38%;
    }
    #signIn, #signUp {
        width: 65%;
    }
    #signIn {
        margin-top: 60%;
    }
}

@media screen and (max-width: 655px) {
    #logoIcon {
        margin-top: 12%;
    }
    #title {
        margin-top: 43%;
    }
    #signIn, #signUp {
        width: 65%;
    }
    #signIn {
        margin-top: 70%;
    }
}

@media screen and (max-width: 586px) {
    #logoIcon {
        margin-top: 15%;
    }
    #title {
        margin-top: 49%;
    }
    #signIn, #signUp {
        width: 70%;
    }
    #signIn {
        margin-top: 80%;
    }
}

@media screen and (max-width: 500px) {
    #logoIcon {
        margin-top: 20%;
    }
    #title {
        margin-top: 58%;
    }
    #signIn, #signUp {
        width: 80%;
    }
    #signIn {
        margin-top: 93%;
    }
}

@media screen and (max-width: 400px) {
    #logoIcon {
        margin-top: 20%;
    }
    #title {
        margin-top: 70%;
    }
    #signIn, #signUp {
        width: 90%;
    }
    #signIn {
        margin-top: 120%;
    }
}
#loginBackground {
    background-image: linear-gradient(#BDFFF3, #4AC29A);
    max-width: 100% !important;
    height: 100vh;
}

#formContainer {
    background-color: rgb(255, 255, 255, .6);
    padding: 5%;
    border-radius: 10px;
    margin: auto;
    margin-top: 10%;
}

#usernameLogin, #passwordLogin {
    background-color: #4AC29A;
    opacity: .75;
    border: none;
    color: white;
}

#formButton {
    background-color: #4AC29A;
    border: none; 
}

@media screen and (max-width: 947px) {
    #formContainer {
        margin-top: 15%;
    }
}

@media screen and (max-width: 767px) {
    #formContainer {
        margin-top: 20%;
    }
}

@media screen and (max-width: 530px) {
    #formContainer {
        margin-top: 30%;
    }
}
#registerBackground {
    background-image: linear-gradient(#BDFFF3, #4AC29A);
    max-width: 100% !important;
    height: 100%;
}

#formContainer {
    background-color: rgb(255, 255, 255, .6);
    padding: 5%;
    border-radius: 10px;
    margin: 5% auto; 
    margin-bottom: 50%;
}

#nameReg, #usernameReg, #emailReg, #dobReg, #passwordReg, #confirmPasswordReg {
    background-color: #4AC29A;
    opacity: .75;
    border: none;
    color: white;
}

#formButton {
    background-color: #4AC29A;
    border: none; 
}

/* background */

.dashboardBackground {
    background-image: linear-gradient(to top, #8f94fb, #8f94fb, #4e54c8);
    height: 100vh;
}

/* avatar */
.avatar {
    margin-left: 16%;
    margin-top: 10%;
}

/* goal container */

.goalContGreeting {
    font-size: 250%;
}

.goalContUser {
    font-size: 350%;
}

.goalContInst {
    font-size: 100%;
    margin-top: 10%;
    color: rgba(0, 0, 0, .6);
}

.goalContainer {
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    background-color: rgb(255, 255, 255, .5);
    width: 100%;
    border-radius: 20px 20px 0 0;
    height: 50%;
    padding-top: 5%;
    position: fixed;
    bottom: 0;
    z-index: 1;
    text-align: center;
}

.closeButton {
    background-color: rgb(78, 84, 200);
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: none;
    margin: 3%;
}

.closeButton:focus {
    outline: none;
}

#closeIcon {
    color: white;
    font-size: 80%;
}

/* nav bar */

.addGoal {
    background-color: rgb(78, 84, 200);
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
    height: 80px;
    width: 80px;
    border-radius: 50%;
    border: none;
    color: white;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 30px;
    z-index: 10;
    text-align: center;
    outline: none;
    padding-top: 6px;
}

.addGoal:focus {
    outline: none;
}

.footer {
    background-color: rgb(238, 240, 244);
    height: 70px;
    width: 100%;
    position: fixed;
    bottom: 0;
    border-radius: 20px 20px 0 0;
    z-index: 5; 
}

.navButton {
    width: 50%;
    height: 100%;
    background-color: transparent;
    border: none;
    font-size: 13px;
}

.navButton:focus {
    outline: none;
}

/* goal card */

.goalCard {
    width: 90%;
    margin: auto;
    background-color: white;
    opacity: .75;
    box-shadow: 0 5px 25px #67686b; 
    margin-bottom: 5%;
    border-radius: 10px;
    transition: box-shadow 0.25s cubic-bezier(0.4, 0, 1, 1);
    text-align: left;
    padding: 5%;
}

#goalIcon {
    margin: 5%;
    margin-top: 0;
    color: #4e54c8;
    font-size: 120%;
    display: inline-block;
}

.goalTitle {
    display: inline-block;
    font-size: 120%;
}

.deleteGoal {
    float: right;
    border: none;
    background: transparent;
}

.deleteGoal:focus {
    outline: none;
}

.progress-bar {
    background-color: #4e54c8 !important;
    box-shadow: 0 2px 10px 0 rgba(143, 148, 251, 1);
}

.goalFr {
    font-size: 100%;
    margin-left: 7%;
    display: inline-block;
}

.goalBtn {
    background-color: #4e54c8;
    border-radius: 5px;
    border: none;
    color: white;
    margin: 5% 2% 0 2%;
    float: right;
    font-size: 92%;
    box-shadow: 0 2px 10px 0 rgba(143, 148, 251, 1);
}

#deleteIcon {
    color: #4e54c8;
    font-size: 120%;
}

.goalScroll {
    height: 77%;
    overflow: scroll;
}

/* modal overlay */

.shade {
    position: absolute;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}

#modal {
    width: 95%;
    margin: auto;
    margin-top: 10%;
    height: 60%;
    border-radius: 10px;
    text-align: center;
}

#input {
    width: 90%;
    margin: auto;
    margin-bottom: 2%;
}

.modalTitle {
    margin-top: 5%;
}

.finishBtn {
    background-color: #4e54c8;
    border-radius: 5px;
    border: none;
    color: white;
    margin: 9%;
    float: right;
    font-size: 92%;
    box-shadow: 0 2px 10px 0 rgba(143, 148, 251, 1);
}
.catBtnBackground {
    position: fixed;
    z-index: 1;
    background-color: rgb(255, 255, 255, 1);
    height: 100vh;
    width: 100vw;
    text-align: center;
}

.catText {
    color: #4e54c8;
    margin-top: 20%;
}

.catBtn {
    color: white;
    background-color: #4e54c8;
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
    border: none;
    border-radius: 10px;
    width: 30%;
    height: 15%;
    margin: 5%;
}

.catBtn:focus {
    outline: none;
}

#spirit {
    display: block;
    margin: auto;
    margin-top: 15%;
}

.goalForm {
    text-align: center;

}

#catLabel {
    font-size: 300%;
    text-shadow: 2px 2px 10px rgba(143, 148, 251, 1);
}

.formLabel {
    color: white;
    display: block;
    font-size: 150%;
    padding-top: 3%;
}

.form-contol {
    width: 90% !important;
}

#formInput {
    width: 90%;
    border: none;
    margin: auto;
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
}

.createGoalBtn {
    background: white;
    border: none;
    border-radius: 10px;
    width: 30%;
    color: #4e54c8;
    margin: 5%;
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
}
.editGoalForm {
    text-align: center;
    padding-top: 3%;
}

.goalEditTitle {
    color: white;
    font-size: 100%;
    margin: 4%;
}

#editCatBtn {
    background-color: white;
    color: #4e54c8;
    border: none;
    box-shadow: 0 2px 20px 0 rgba(143, 148, 251, 1);
    margin: 2%;
}
/* social nav */

.socialBackground {
    background-image: linear-gradient(to top, #a8e063,#a8e063, #56ab2f);
    height: 100vh;
}

#addPost {
    background-color: #56ab2f;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
}

/* post card */

.postCont {
    padding-top: 5%;
    height: 86%;
    overflow: scroll;
}

.postCard {
    width: 95%;
    margin: auto;
    background-color: rgb(255, 255, 255, .75);
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
    margin-bottom: 5%;
    border-radius: 10px;
    text-align: left;
    padding: 2%;
}

.postUser {
    display: inline;
    font-size: 100%;
    margin-left: 5%;
}

.postBody {
    width: 90%;
    margin: auto;
}

.postPic {
    width: 100%;
    border-radius: 10px;
}

.postCreate {
    /* display: inline-block; */
    font-size: 1%;
    float: right;
    padding-top: 4%;
}

.likeCont {
    padding-top: 8%;
}

.postLikes {
    display: inline-block;
    font-size: 100%;
}

#likeIcon {
    display: inline-block;
    font-size: 150%;
    padding-left: 3%;
}
.postForm {
    color: white;
    text-align: center;
    padding-top: 6%;
}

.postTitle {
    padding-bottom: 6%;
}

#postFormInput {
    width: 90%;
    border: none;
    margin: auto;
    background-color: rgb(255, 255, 255, .75);
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
}

.postBtns {
    padding-top: 10%;
}

#postBtnGoal {
    display: inline-block;
    border: none;
    background-color: rgb(255, 255, 255, .75);
    border-radius: 5px;
    padding: 1%;
    margin: 2%;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
    float: left;
    margin-left: 15%;
}

#postBtn {
    border: none;
    background-color: rgb(255, 255, 255, .75);
    border-radius: 5px;
    padding: 2%;;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
    color: #56ab2f;
    font-size: 120%;
}

#postBtnData {
    border: none;
    background-color: rgb(255, 255, 255, .75);
    border-radius: 5px;
    padding: 2%;;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
    color: #56ab2f;
    font-size: 120%;
    opacity: .5;
}

.postInfo {
    display: inline;
}

#postSubmit {
    background: white;
    border: none;
    border-radius: 10px;
    width: 30%;
    color: #56ab2f;;
    margin: 5%;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
}

#postSubmitD {
    background: rgb(255, 255, 255, .5);
    border: none;
    border-radius: 10px;
    width: 30%;
    color: #56ab2f;;
    margin: 5%;
    box-shadow: 0 2px 20px 0 rgba(168, 225, 99, 1);
}




